import Vue from 'vue'
import Router from 'vue-router'
import product from '@/components/product'
import productSlider from '@/components/productSlider'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'productSlider',
      component: productSlider
    },
    {
      path: '/:id',
      name: 'product',
      component: product,
      props: true
    }
  ]
})



