<template>
<div class="preload" v-show="dataReady">data load</div>
</template>

<script>
import Firebase from 'firebase'

let config = {
  apiKey: 'AIzaSyCq0FRyaIttnlj9IQz9-OCKgcbi-ibqHr0',
  authDomain: 'ember-390fd.firebaseapp.com',
  databaseURL: 'https://ember-390fd.firebaseio.com',
  projectId: 'ember-390fd',
  storageBucket: 'ember-390fd.appspot.com',
  messagingSenderId: '907924565990'
}

let app = Firebase.initializeApp(config)
let db = app.database()

export default {
  name: 'getData',
  firebase: {
    products: db.ref('products/'),
    recomended: db.ref('recomended/')
  },
  data () {
    return {
      dataLoad: false
    }
  },
  methods: {},
  computed: {
    dataReady () {
      if (parseInt(this.products.length) <= 0 || parseInt(this.recomended.length) <= 0) {
        return this.dataLoad === true
      }
    }
  },
  created () {

  }
}
</script>

<style lang="scss" scoped>
.preload {
  background-color: red;
}
</style>



