<template>
  <div id="app" class="container" v-on:scroll="onScroll">
    <getData />
    <navigation v-bind:scrolled="scrolled" v-bind:scrollY="scrollY" />
    <router-view/>
  </div>
</template>

<script>
import navigation from './components/navigation.vue'
import getData from './components/getData.vue'
export default {
  name: 'app',
  components: {
    navigation, getData
  },
  data () {
    return {
      scrolled: false,
      scrollY: 0
    }
  },
  methods: {
    onScroll () {
      this.scrolled = window.scrollY > 0
      this.scrollY = window.scrollY
    }
  },
  created () {
    window.addEventListener('scroll', this.onScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.onScroll)
  }
}
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700|Oswald:700');
html {
  font-family: 'Open Sans', sans-serif;
  font-kerning: normal;
  font-size: 10px;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
}
#app {
  text-align: left;
}
.container {
  max-width: 835px;
  margin: 0 auto;
}
</style>



