<template>
<div class="header" v-bind:class="{ scrolled: scrolled }" v-bind:style="topPostion">
  <h1 class="header__caption">Select product</h1>
</div>
</template>

<script>
export default {
  name: 'navigation',
  props: ['scrolled', 'scrollY'],
  methods: {},
  computed: {
    topPostion: function () {
      return {
        transform: 'translateY(' + this.scrollY + 'px)'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #ffffff;
  opacity: 0.9;
  height: 50px;
  &__caption {
    margin: 0;
  }
}
</style>



