<template>
<div class="recomended-slider">
  <div class="recomended__list-item" v-for='item in recomended' v-bind:key="item.id">
    <router-link v-bind:href="item.id" v-on:click="getId" class="recomended__list-link" v-bind:to='item.id'>
      <img class="recomended__list-img" alt="" v-bind:src="item.img" />
      <p class="recomended__list-link__price">
        <span class="recomended__list-link__price-old" v-if="item.price.old !== ''">{{ item.price.old }}</span>
        <span class="recomended__list-link__price-current">{{ item.price.current }}</span>
      </p>
    </router-link>
  </div>
</div>
</template>

<script>
import getData from './getData.vue'

export default {
  name: 'productSlider',
  extends: getData,
  props: ['id'],
  data () {
    return {
      recomended: this.recomended
    }
  },
  methods: {
    getId: function (event) {}
  }
}
</script>

<style lang="scss" scoped>
.recomended {
  &-slider {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__list {
    &-img {
      display: block;
    }
    &-item {}
    &-link {
      display: block;
      position: relative;
      text-decoration: none;
      margin: 20px 0 0 0;
      &__price {
        position: absolute;
        background-color: #8ae6cf;
        right: 0;
        bottom: 0;
        color: #2c303d;
        font-size: 14px;
        font-weight: 700;
        padding: 0 5px;
        &-old,
        &-current {
          &:before {
            content: '\0024';
            display: inline;
          }
        }
        &-old {
          text-decoration: line-through;
        }
      }
    }
  }
}
</style>



