<template>
  <div class="rating">
    <span class="rating__default" v-bind:title="'Current rating ' + rating / 2">
      <span class="rating__current" v-bind:style="{ width : rating * 9  + 'px'}"></span>
    </span>
  </div>
</template>

<script>
export default {
  name: 'ratingStar',
  props: ['rating']
}
</script>

<style lang="scss" scoped>
.rating {
  &__default,
  &__current {
    display: inline-block;
    height: 18px;
  }
  &__default {
    background: svg-load('../../static/service/grey-star.svg', fill=#ababab, stroke=#ababab);
    width: 90px;

  }
  &__current {
    background: svg-load('../../static/service/grey-star.svg', fill=#ce2c52, stroke=#ce2c52);
      position: relative;
  }
}
</style>



