<template>
<div
  class="size"
  v-if="size"
>
  <form>
    <ul class="size__list">
      <li class="size__list-item" v-for="value in size" v-bind:key="value.key">
        <input
          type="radio"
          v-bind:value="value"
          v-bind:id="'size-' + value"
          v-model="selectedSize"
          name="product__size-input"
          class="size__list-item-input"
        />
        <label class="size__list-item-label" v-bind:for="'size-' + value">{{ value }}</label>
      </li>
    </ul>
    <button
      class="size__cart-btn"
      v-on:click.prevent="getSize()"
    >add to cart</button>
  </form>
</div>
</template>

<script>
export default {
  name: 'selectSize',
  props: ['size'],
  data () {
    return {
      selectedSize: ''
    }
  },
  methods: {
    getSize: function () {
      event.preventDefault()
      console.log(this.selectedSize)
    }
  }
}
</script>

<style lang="scss" scoped>
.size {
  &__list {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    &-item {
      margin: 0 5px;
      &:first-child {
        margin: 0 5px 0 0;
      }
      &-input {
        display: none;
        &:checked + label {
          background-color: #2c303d;
          color: #ffffff;
        }
      }
      &-label {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #565656;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 700;
        line-height: 0;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
  &__cart{
    &-btn {
      font-family: 'Open Sans', sans-serif;
      font-size: 14px;
      font-weight: 700;
      text-transform: uppercase;
      text-align: center;
      background-color: #8ae6cf;
      padding: 15px 45px;
      border: 1px solid #8ae6cf;
      border-radius: 30px;
      line-height: 11px;
      letter-spacing: 0.3px;
      margin: 25px 0;
      max-width: 215px;
      width: 100%;
      &:hover {
        cursor: pointer;
      }
    }
  }
}
</style>



