<template>
<section>
  <div class="product">
    <div class="product__img">
      <img v-bind:src='recomended[getIdProd()].img' alt="" />
    </div>
    <div class="product__info">
      <h3 class="product__info-title">{{ recomended[getIdProd()].title }}</h3>
      <span class="product__info-id">Product id# {{ recomended[getIdProd()].id }}</span>
      <ratingStar v-bind:rating="recomended[getIdProd()].rating" />
      <div class="product__price">
        <span class="product__price-old" v-if="recomended[getIdProd()].price.old.length > 0">{{ recomended[getIdProd()].price.old }}</span>
        <span class="product__price-new">{{ recomended[getIdProd()].price.current }}</span>
      </div>
      <p class="product__info-description">{{ recomended[getIdProd()].description }}</p>
      <selectSize v-bind:size="recomended[getIdProd()].size" />
    </div>
  </div>
  <h1>YOU MAY ALSO LIKE</h1>
  <productSlider />
</section>
</template>

<script>
import getData from './getData.vue'
import selectSize from './selectSize.vue'
import ratingStar from './ratingStar.vue'
import productSlider from './productSlider.vue'

export default {
  extends: getData,
  name: 'product',
  components: {
    productSlider, selectSize, ratingStar
  },
  data () {
    return {
      recomended: this.recomended
    }
  },
  methods: {
    getIdProd: function () {
      return parseInt((this.$route.params.id) - 1)
    }
  },
  created () {
    console.log('created')
  }
}
</script>

<style lang="scss" scoped>
.product {
  display: flex;
  flex-direction: row;
  &__name {}
  &__img-wrapp {
    &-img {}
  }
  &__info {
    margin: 0 0 0 20px;
    &-title {
      font-family: Oswald,sans-serif;
      font-size: 27px;
      line-height: .8;
      letter-spacing: -1.2px;
      word-spacing: .2px;
      margin-top: 0;
    }
    &-id {
      color: #2c303d;
      font-size: 10px;
      font-weight: 400;
      margin: 7px 0;
      letter-spacing: .7px;
    }
    &-description {
      line-height: 16px;
      margin: 25px 0;
      font-size: 12px;
    }
  }
  &__price {
    font-size: 24px;
    font-weight: 700;
    color: #ce2c52;
    margin: 13px 0 9px;
    &-old,
    &-new {
      &:before {
        content: '\0024';
        display: inline;
      }
    }
    &-old {
      color: #2c303d;
      text-decoration: line-through;
    }
  }
}
</style>



